.container{
    /* width: 80px; */
    -ms-flex:1 1;
        flex:1 1;
    min-width: 80px;
    max-width: 180px;
    height: inherit;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    background: rgb(35,35,35);
    border-top: 1px white solid;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.wrapper{
    -ms-flex: auto;
        flex: auto;
    width: 100%;
    overflow-y: auto;
    /* box-sizing: border-box; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
    /* background: rgb(31,31,31); */
}
.item{
    -ms-flex:none;
        flex:none;
    display: block;
    width: 100%;
    cursor: pointer;
    height: 40px;
    /* padding: 0 4px; */
    font-family: "微软雅黑";
    line-height: 40px;
    font-size: 16px;
    /* color: #ccc; */
    text-align: center;
    overflow: hidden;
    /* white-space: nowrap; */
    /* text-overflow: ellipsis; */
    text-decoration: none;
    border-radius: 5px;
    -webkit-transition: font-size 0.5s ease-in-out;
    -o-transition: font-size 0.5s ease-in-out;
    transition: font-size 0.5s ease-in-out;
}
.item:hover{
    background: #E8E8E8;
    /* height: 55px; */
    /* line-height: 55px; */
    font-size: 18px;    
}

/* 隐藏按钮 */
.list{
    height: 45px;
    padding: 10px 10px;
    font-size: 25px;
    color:#000;
    text-align: right;
}
.selected{
    background:rgb(255, 99, 14);
    color: #000;
}
